import Typography from '@mui/material/Typography';

// 
export default function EllipsisText({text, lines, ...props}: any) {
  return (
    <Typography
      {...props}
      sx={{
        width: "100%",
        display: "-webkit-box",
        WebkitBoxOrient: "vertical",
        WebkitLineClamp: lines,
        overflow: "hidden",
      }}
    >
      {text}
    </Typography>
  );
}
