import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery'

import Header from '../components/Header';
import Footer from '../components/Footer';

const siteName = 'RECT-ANGLE lab.';

const sections = [
  { title: 'Home', url: `${process.env.PUBLIC_URL}/` },
  { title: 'Privacy Policy', url: `${process.env.PUBLIC_URL}/privacy-policy` },
];

// 
export default function AppBase({children, lang}: {children: React.ReactNode, lang?: string | undefined}) {
  // 
  const isDarkMode = useMediaQuery('(prefers-color-scheme: dark)')
  const theme = createTheme({
    palette: {
      mode: isDarkMode ? 'dark' : 'light'
    },
    typography: {
      fontSize: (lang && lang === 'ja') ? 12 : 14,
      fontFamily:  ['Noto Sans JP'].join(','),
    },
  });

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth="lg">
        <Header title={siteName} sections={sections} />
        <main>
          {children}
        </main>
      </Container>
      <Footer title={siteName} description="Take it easy!"/>
    </ThemeProvider>
  );
}
