import Grid from '@mui/material/Grid';

import Markdown from './Markdown';

// 
export default function Main({post}: {post: string}) {
  return (
    <Grid item xs={12} md={8} sx={{'& .markdown': {py: 3}}}>
      <Markdown className="markdown" key={post.substring(0, 40)}>
        {post}
      </Markdown>
    </Grid>
  );
}
